header {
  background-color: #fff;
  height: auto;
  padding: 10px;
  box-shadow: 0 5px 30px rgb(0 22 84 / 10%);
  padding: 5px 0;
  position: sticky;
  font-weight: 900;
  top: 0;
  z-index: 100;
}
header img {
  width: 180px;
  padding: 10px;
}
.icons {
  text-align: center;
}

.icons a i {
  margin: 10px;
  font-size: 30px;
  color: floralwhite;
}

.icons a i:hover {
  transform: scale(1.1);
}

.hero-btn {
  display: inline-block;
  text-decoration: none;
  text-align: center;
  color: #fff;
  margin-top: 30px;
  border: 1px solid #fff;
  padding: 5px 30px;
  font-size: 13px;
  background: transparent;
  position: relative;
  cursor: pointer;
}

.hero-btn:hover {
  border: 1px solid #379e37;
  background-color: #379e37;
  transition: 1s;
}
header a {
  margin-left: 30px;
  transition: 0.5s;
  font-weight: 500;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
}
header a:hover {
  color: #379e37;
}
header span {
  width: auto;
  padding: 0px 6px;
  background: #2771ae;
  border-radius: 50px;
  color: #ffffff;
  margin-right: 4px;
  font-weight: 400;
}
header h4 {
  font-weight: 300;
  margin-right: 20px;
}
header .toggle {
  display: none;
}
.language-menu {
  border-radius: 5px;
  padding: 10px 2px;
  background: #36a036;
  border: none;
  color: #fff;
  cursor: pointer;
  font-weight: bold;
  margin: 0px 5px 15px 5px;
  font-size: 11px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}
.language-menu option {
  font-size: 15px;
}

@media screen and (max-width: 903px) {
  header {
    height: auto;
    padding-top: 10px;
  }
  header img {
    width: 130px;
  }
  header .button {
    display: none;
  }
  header .nav .flex {
    display: none;
  }
  header .toggle button {
    padding: 10px 15px;
  }
  header .toggle {
    display: block;
    margin-top: 1px;
  }
  header .small {
    position: absolute;
    top: 9.2vh;
    left: 0;
    width: 100%;
    background-color: #276dae;
    padding: 20px;
  }
  header li a {
    display: inline-block;
    margin-bottom: 20px;
    color: #fff;
  }
  .language-menu option {
    font-size: 8px;
  }
}

@media screen and (max-width: 800px) {
  header img {
    margin-bottom: -8px;
    margin-left: -20px;
  }
  header .button {
    display: none;
  }
  header .nav .flex {
    display: none;
  }
  header .toggle {
    display: block;
    margin-top: 2px;
  }
  header .small {
    position: absolute;
    top: 9.2vh;
    left: 0;
    width: 100%;
    background-color: #276dae;
    padding: 20px;
  }
  header li a {
    display: inline-block;
    margin-bottom: 20px;
    color: #fff;
  }
}
